function isChildElement(x) {
    return x instanceof HTMLElement || Array.isArray(x);
}
export class Component {
    constructor(element, settings, events) {
        this.element = element;
        this.settings = settings;
        this.events = events;
        this.cache = {};
    }
    ensure(root, query, isRequired = true) {
        if (!root)
            throw new Error(`Root element not found`);
        if (!query)
            return root;
        if (!this.cache[query]) {
            const el = this.element.querySelector(query);
            if (el)
                this.cache[query] = el;
            else if (isRequired)
                throw new Error(`Element not found`);
        }
        return this.cache[query];
    }
    setChildren(root, childs) {
        // ...         
        root.replaceChildren(...(Array.isArray(childs) ? childs : [childs]));
    }
    setElement(query, value) {
        const el = this.ensure(this.element, query);
        el.replaceWith(this.ensure(value));
    }
    //      
    setValue(query, value, isRequired = true) {
        const el = query instanceof HTMLElement ? query : this.ensure(this.element, query);
        if (typeof value === 'string')
            el.textContent = value;
        else if (isChildElement(value)) {
            this.setChildren(el, value);
            console.log('value');
        }
        else if (typeof value === 'object')
            Object.assign(el, value);
        else {
            throw new Error(`Unknown value type`);
        }
    }
    render(data) {
        Object.assign(this, data !== null && data !== void 0 ? data : {});
        return this.element;
    }
}
