import { Component } from "../base/Component";
export class OrderSuccess extends Component {
    constructor(element, settings, events) {
        super(element, settings, events);
        const button = this.ensure(this.element, this.settings.button);
        button.addEventListener('click', () => {
            this.events.emit('order:completed');
        });
    }
    set total(value) {
        this.setValue(this.settings.description, ` ${value} `);
    }
}
