export class ProductsData {
    constructor(events) {
        this.events = events;
    }
    set items(items) {
        this._items = items;
        this.events.emit('products:changed');
    }
    get items() {
        return this._items;
    }
    set preview(id) {
        this._preview = id;
        this.events.emit('product:selected');
    }
    get preview() {
        return this._preview;
    }
    getProduct(id) {
        return this._items.find(item => item.id === id);
    }
}
