const validate = require("validate.js");
export class OrderData {
    constructor(events) {
        this.events = events;
        this._orderInfo = {
            payment: '',
            address: '',
            email: '',
            phone: '',
        };
        this._items = [];
    }
    set items(items) {
        this._items = items;
        this.events.emit('items:changed');
    }
    get items() {
        return this._items;
    }
    set orderInfo(data) {
        Object.assign(this._orderInfo, data);
    }
    get orderInfo() {
        return this._orderInfo;
    }
    get total() {
        return this._items.map(item => {
            if (typeof item.price === 'number') {
                return item.price;
            }
            else {
                return 0;
            }
        }).reduce((a, b) => a + b, 0);
    }
    get order() {
        const order = Object.assign(Object.assign({}, this._orderInfo), { total: this.total, items: this._items.map(item => item.id) });
        return order;
    }
    addItem(item, callback) {
        this._items = [...this._items, item];
        if (callback) {
            callback();
        }
        this.events.emit('items:changed');
    }
    deleteItem(id, callback) {
        this._items = this._items.filter(item => item.id !== id);
        if (callback) {
            callback();
        }
        this.events.emit('items:changed');
    }
    clear() {
        this._items = [];
        this.events.emit('items:changed');
    }
    checkFormValidation(data, constraints) {
        const isValid = validate(data, constraints);
        return isValid;
    }
    checkFieldValidation(data, constraints) {
        const result = validate.single(data.value, constraints[data.field]);
        if (result) {
            return result[0];
        }
        else {
            return '';
        }
    }
    pushOrder(callback) {
        return callback(this.order);
    }
}
