export class AppApi {
    constructor(baseApi) {
        this._baseApi = baseApi;
    }
    getProducts() {
        return this._baseApi.get('/product/').then((products) => products);
    }
    getProduct(id) {
        return this._baseApi.get(`/product/${id}`).then((product) => product);
    }
    pushOrder(order) {
        return this._baseApi.post('/order', order, 'POST').then((response) => response);
    }
}
